import uniList from '@/components/uni-list/uni-list.vue'
import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
	export default {
		data() {
			return {
				
			}
		},
		methods: {
			gotoPage(action)
			{
				switch (action){
					case 1://游戏记录
						uni.navigateTo({
							url: '/pages/youxiajilu/youxiajilu'
						});
						break;
					case 2://游戏统计
						uni.navigateTo({
							url: '/pages/youxitongji/youxitongji'
						});
						break;
					case 3://上下分记录
						uni.navigateTo({
							url: '/pages/shangxiafenjilu/shangxiafenjilu'
						});
						break;
					case 4://游戏协议
						uni.navigateTo({
							url: '/pages/xieyi/xieyi'
						});
						break;
					case 5://修改密码
						uni.navigateTo({
							url: '/pages/xiugaimima/xiugaimima'
						});
						break;
					case 6://联系我们
						uni.switchTab({
							url: '/pages/callcenter/callcenter'
						});
					case 7://登出
						this.loginOut()
						break;
					default:
						break;
				}
			},
			loginOut() {
				this.bus.request({
					url: this.bus.url+'/user/logout',
					method: 'GET',
					 header:{
					  'content-type':'application/x-www-form-urlencoded',
					},
					data: this.bus.login,
					success: res => {
						res=res.data;
						if(res.status=='1')
						{
							this.bus.login={};
							this.bus.user={};
							uni.redirectTo({
								url: '/pages/login/login'
							});
						} else{
							uni.showToast({
								title: res.errMsg,
								duration: 2000,
								icon:'none'
							});
						}
					},
					fail: () => {
						
					},
					complete: () => {
						uni.hideLoading()
					}
				});
			}
		},components: {
			uniList,
			uniListItem
		}
	}
